import React, { Component } from "react";
import UserService from "../../services/user.service";
import Form from "react-bootstrap/Form";
import TutorialDataService from "../../services/TutorialService"
export default class CC extends Component {
  constructor(props) {
    super(props);
    this.state = {
        currentClient:{
          id:null,
          name:'',
          code:'',
          Succ:'',
          numEquipe:0,
          chef:false
        }

        };
    this.onChangeName = this.onChangeName.bind(this);
    this.onChangeCode = this.onChangeCode.bind(this);
    this.onChangeSucc = this.onChangeSucc.bind(this);
    this.deleteClient = this.deleteClient.bind(this);
    this.updateClient = this.updateClient.bind(this);
    this.checkboxHandler = this.checkboxHandler.bind(this);
    this.onChangeNum=this.onChangeNum.bind(this);
  }
  componentDidMount() {
    this.getClient(this.props.match.params.id);
    
  }

  onChangeName(e) {
    const name = e.target.value;

    this.setState(function(prevState) {
      return {
        currentClient: {
          ...prevState.currentClient,
          name: name
        }
      };
    });
  }
  onChangeCode(e) {
    const code = e.target.value;
    
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        code: code
      }
    }));
  }

  onChangeSucc(e) {
    const succ = e.target.value;
    
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        Succ: succ
      }
    }));
  }
  onChangeNum(e){
    const num = e.target.value;
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        numEquipe: num
      }
    }));
    console.log("from"+num)
    console.log("state"+this.state.numEquipe)
  }
  checkboxHandler= (e) => {
    // revoir
    var str2bool = (value) => {
      if (value && typeof value === "string") {
           if (value.toLowerCase() === "true") return true;
           if (value.toLowerCase() === "false") return false;
      }
      return value;
   }
    
    console.log(e.target.value)
    const bool =str2bool(e.target.value);
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        chef: bool
      }
    }));
    console.log("chef ?  :"+ this.state.chef)
  }
  getClient(id) {
    TutorialDataService.getC(id)
      .then(response => {
        this.setState({
          currentClient: response.data
        });
        console.log(response.data);
        //console.log(this.state.currentClient.prenom)
      })
      .catch(e => {
        console.log(e);
      });
  }

  updateClient() {
    UserService.updateC(
      this.state.currentClient.id,
      this.state.currentClient
    )
      .then(response => {
        console.log(this.state.currentClient.numEquipe)
        this.setState({
          message: "The client was updated successfully!"
        });
        this.props.history.push('/admin')
      })
      .catch(e => {
        console.log(e);
      });
  }

  deleteClient() {    
    this.props.history.push('/admin');
}
  render(){
    const { currentClient } = this.state;
    console.log(currentClient);
  return (

    <div>
      {currentClient ? (
        <div className="edit-form">
            <Form >
                <Form.Group controlId="formBasicName">
                  <Form.Label>Nom</Form.Label>
                  <Form.Control type="text" value={currentClient.nom} onChange={this.onChangeName} placeholder={currentClient.nom} />
                </Form.Group>
                
                <Form.Group controlId="formBasicCode">
                    <Form.Label>AX</Form.Label>
                    <Form.Control type="text" value={currentClient.code} onChange={this.onChangeCode} placeholder={currentClient.ax} />
                </Form.Group>
                <Form.Group controlId="formBasicSucc">
                    <Form.Label>Succersale</Form.Label>
                    <Form.Control type="text" value={currentClient.Succ} onChange={this.onChangeSucc} placeholder={currentClient.succ} />
                </Form.Group>
                <Form.Group controlId="formBasicNum">
                    <Form.Label>Numero d'equipe</Form.Label>
                    <Form.Control type="number" value={currentClient.numEquipe} onChange={this.onChangeNum} placeholder={currentClient.numEquipe} />
                </Form.Group>
                
               {/* <Form.Group controlId="formBasicChef">
                    <Form.Label>chef</Form.Label>
                    <Form.Control type="checkbox" value={currentClient.chef} onChange={this.checkboxHandler}/>
                </Form.Group>
              */}
                <Form.Group controlId="formBasicChef"  onChange={this.checkboxHandler}>
                  <Form.Label>Chef</Form.Label>
                  <Form.Control as="select"  size="sm" value={currentClient.chef}>
                    <option value="" disabled selected>Select</option>
                    <option value='true'>Chef</option>
                    <option value='false'>Commerciale</option>
                  </Form.Control>
                </Form.Group>

              </Form>

              <button
              type="submit"
              className="badge badge-success"
              onClick={this.updateClient}
              
            >
              Mettre  jour
            </button>
            <button
              className="badge badge-danger mr-2"
              onClick={this.deleteClient}
              
            >
              retour
            </button>
            
            <p>{this.state.message}</p>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Client...</p>
          </div>
        )}
      </div>
    );
  }
  
}

