import React, { Component } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import userService from "../services/user.service";
import AuthService from "../services/auth.service";
//import userService from "../services/user.service";

const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        This is not a valid email.
      </div>
    );
  }
};

const vusername = value => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        The username must be between 3 and 20 characters.
      </div>
    );
  }
};

const vpassword = value => {
  if (value.length < 6 || value.length > 40) {
    return (
      <div className="alert alert-danger" role="alert">
        The password must be between 6 and 40 characters.
      </div>
    );
  }
};

export default class Register extends Component {
  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.checkboxHandler = this.checkboxHandler.bind(this);
    this.onChangeNum=this.onChangeNum.bind(this);
    this.onChangeAx=this.onChangeAx.bind(this);
    this.onChangeSucc=this.onChangeSucc.bind(this);
    this.state = {
      username: "",
      email: "",
      password: "",
      successful: false,
      message: "",
      ax:"",
      succ:"",
      numEquipe:0,
      chef:false
    };
  }

  onChangeUsername(e) {
    this.setState({
      username: e.target.value
    });
  }

  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }  
  onChangeNum(e){
    this.setState({
      numEquipe:e.target.value
    });
  }
  onChangeSucc(e){
    this.setState({
      succ:e.target.value
    });
  }
  
  onChangeAx(e){
    this.setState({
      ax:e.target.value
    });
  }
checkboxHandler= (e) => {
  const bool = this.state.chef;
  this.setState({chef:!bool});
}
  handleRegister(e) {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false
    });

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.register(
        this.state.username,
        this.state.email,
        this.state.password,
        this.state.chef,
      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage
          });
        }
      );
      
         userService.addC(this.state.username,this.state.ax, this.state.succ,this.state.numEquipe,this.state.chef)
         .then(response => {
          console.log("cc added")
        },
        error => {
          console.log("erRRoooooor")
        }
      );

    }
  }

  render() {
    return (
      <div className="col-md-12">
        <div className="card card-container">
          <img
            src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
            alt="profile-img"
            className="profile-img-card"
          />

          <Form
            onSubmit={this.handleRegister}
            ref={c => {
              this.form = c;
            }}
          >
            {!this.state.successful && (
              <div>
                <div className="form-group">
                  <label htmlFor="username">Username</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="username"
                    value={this.state.username}
                    onChange={this.onChangeUsername}
                    validations={[required, vusername]}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="email"
                    id="current-email"
                    autocomplete="current-email"
                    value={this.state.email}
                    onChange={this.onChangeEmail}
                    validations={[required, email]}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="password">Password</label>
                  <Input
                    type="password"
                    className="form-control"
                    name="password"
                    id="current-password"
                    autocomplete="current-password"
                    value={this.state.password}
                    onChange={this.onChangePassword}
                    validations={[required, vpassword]}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="text">Numero d'equipe</label>
                  <Input
                    type="number"
                    className="form-control"
                    name="Numero d'equipe"
                    value={this.state.numEquipe}
                    onChange={this.onChangeNum}
                    //validations={[required, vnum]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="text">Succursale</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="Succersale"
                    value={this.state.succ}
                    onChange={this.onChangeSucc}
                    //validations={[required, vnum]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="text">Code Vendeur</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="ax"
                    value={this.state.ax}
                    onChange={this.onChangeAx}
                    //validations={[required, vsucc]}
                  />
                </div>
                <div className="form-group">
                  <label>Chef d'equipe:</label>
                  <input type="checkbox" name="nom null" value={this.state.chef} 
                      defaultChecked={this.state.chef} onChange={this.checkboxHandler} />
                    
                </div>

                <div className="form-group">
                  <button className="btn btn-primary btn-block">Sign Up</button>
                </div>
              </div>
            )}

            {this.state.message && (
              <div className="form-group">
                <div
                  className={
                    this.state.successful
                      ? "alert alert-success"
                      : "alert alert-danger"
                  }
                  role="alert"
                >
                  {this.state.message}
                </div>
              </div>
            )}
            <CheckButton
              style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />
          </Form>
        </div>
      </div>
    );
  }
}
