import React, { Component } from "react";
import { Switch, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import AuthService from "./services/auth.service";
import Login from "./components/login.component";
import Register from "./components/register.component";
import Home from "./components/home.component";
import Profile from "./components/profile.component";
import BoardUser from "./components/board-user.component";
import BoardModerator from "./components/board-moderator.component";
import BoardAdmin from "./components/board-admin.component";
import Client from "./components/client.component";
import ClientsList from "./components/client-list.component";
import TutorialsList from "./components/Layout/TutorialList";
import CCList from "./components/Layout/CCList";
import CC from "./components/Layout/CC";
import BoardModeratorChart from "./components/board-moderator-chart.component";
import BoardCallCenter from "./components/call_center.component"
import UploadFiles from "./components/Layout/UploadFiles";
import BoardCC from "./components/board-cc.component";
import Predict1 from "./components/Predict1";
class App extends Component {
  constructor(props) {
    super(props);
    this.logOut = this.logOut.bind(this);
    this.state = {
      showUserBoard:false,
      showModeratorBoard: false,
      showAdminBoard: false,
      showCCBoard:false,
      currentUser: undefined,
    };
  }

  componentDidMount() {
    const user = AuthService.getCurrentUser();
    if (user) {
      this.setState({
        currentUser: user,
        showUserBoard : user.roles.includes("ROLE_CALL_CENTER"),
        showModeratorBoard: user.roles.includes("ROLE_CC"),
        showAdminBoard: user.roles.includes("ROLE_MARKETING"),
        showCCBoard: user.roles.includes("ROLE_USER"),
      });
    }
  }

  logOut() {
    AuthService.logout();
  }

  render() {
    const { currentUser,showUserBoard, showModeratorBoard, showAdminBoard,showCCBoard } = this.state;

    return (
      <div>
        <nav className="navbar navbar-expand navbar-dark bg-dark">
          <Link to={"/"} className="navbar-brand">
            Renault
          </Link>
          <div className="navbar-nav mr-auto">
            <li className="nav-item">
              <Link to={"/home"} className="nav-link">
                Home
              </Link>
            </li>
            {/*hna*/}
            {showUserBoard && (
              <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/mod2"} className="nav-link">
                  Call Center
                </Link>
              </li>
              <li className="nav-item">
                <Link to={"/predict1"} className="nav-link">
                  Client potentiel
                </Link>
              </li>
              </div>
            )}
            {showCCBoard && (
              <li className="nav-item">
                <Link to={"/mod1"} className="nav-link">
                  Liste de clients
                </Link>
              </li>
            )}
            {showModeratorBoard && (
              <li className="nav-item">
                <Link to={"/mod"} className="nav-link">
                  Liste des clients
                </Link>
              </li>
            )}

            {showAdminBoard && (
              <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/admin"} className="nav-link">
                  Liste des Commerciaux
                </Link>
              </li>
              <li className="nav-item">
              <Link to={"/chart"} className="nav-link">
                chart
              </Link>
              </li>
              <li className="nav-item">
                <Link to={"/file"} className="nav-link">
                  Add File 
                </Link>
              </li>
              <li className="nav-item">
                <Link to={"/register"} className="nav-link">
                  Add User
                </Link>
              </li>
              
              </div>
            )}
{/*
            {currentUser && (
              <>
              <li className="nav-item">
                <Link to={"/clients"} className="nav-link">
                  Liste des clients
                </Link>
              </li>
             
              <li className="nav-item">
                <Link to={"/add"} className="nav-link">
                  ADD Client
                </Link>
              </li>
              
              </>
            )}
            */}
          </div>

          {currentUser ? (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/profile"} className="nav-link">
                  {currentUser.username}
                </Link>
              </li>
              <li className="nav-item">
                <a href="/" className="nav-link" onClick={this.logOut}>
                  LogOut
                </a>
              </li>
            </div>
          ) : (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/login"} className="nav-link">
                  Login
                </Link>
              </li>
              {/*
              <li className="nav-item">
                <Link to={"/register"} className="nav-link">
                  Sign Up
                </Link>
              </li>
              */}
            </div>
          )}
        </nav>

        <div >
        <div className="container mt-3">
          <Switch>
            <Route exact path={["/", "/home"]} component={Home} />          
            <Route exact path="/login" component={Login} />
            <Route exact path="/register" component={Register} />
            <Route exact path="/profile" component={Profile} />
            <Route path="/user" component={BoardUser} />
            <Route path="/mod" component={BoardModerator} />
            <Route path="/mod1" component={BoardCC} />
            <Route path="/admin" component={BoardAdmin} />
            <Route path="/chart" component={BoardModeratorChart} />
            <Route path="/api/clients/:id" component={Client} />
            <Route exact path="/clients" component={ClientsList} />
  {/*       <Route exact path="/add" component={AddTutorial} /> */}
      {/*<Route exact path="/file" component={File}/>*/}
            <Route exact path ="/mod2" component={BoardCallCenter}/>
            <Route exact path="/file" component={UploadFiles}/>
            <Route exact path="/slm" component={TutorialsList} />
            <Route exact path="/cc" component={CCList}/>
            <Route path="/cc/:id" component={CC} />
            
            <Route path="/predict1" component={Predict1} />
          </Switch>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
