import React, { useState, useEffect, useMemo, useRef } from 'react';
import Pagination from "@material-ui/lab/Pagination";
import TutorialDataService from "../../services/TutorialService";
import { useTable } from "react-table";
import { useHistory } from "react-router-dom";
import { Button } from 'primereact/button';
//import userService from '../../services/user.service'
//import {Checkbox} from 'primereact/checkbox'
const CCList = (props) => {

    const [cc, setCc] = useState([]);
    const [searchTitle, setSearchTitle] = useState("");  
    const tutorialsRef = useRef();
    
    
    //conditions:  
    //const [checked, setChecked] = useState(false);
    const [named,setNamed] = useState(false);
    const [both,setBoth] = useState(false);
    const [succ,setSucc] = useState();
    const [num,setNum] = useState(0);
    //page -- count  --  pageSize 
    const [page, setPage] = useState(1);
    const [count, setCount] = useState(0);
    const [pageSize, setPageSize] = useState(6);
    const history = useHistory()
    const pageSizes = [3,6,9];
  
    tutorialsRef.current = cc;
  
    const onChangeSearchTitle = (e) => {
      const searchTitle = e.target.value;
      setSearchTitle(searchTitle);
    };
    const findByAx = () =>{
      console.log(searchTitle);
      TutorialDataService.findByAx(searchTitle)
      .then(response => {     
        //const data = JSON.parse(JSON.stringify(response.data));
        const { cc, totalPages } = response.data;

        setCc(cc); 
        setCount(totalPages);
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
    }
  
    const getRequestParams = (searchTitle, page, pageSize,numEquipe) => {
      let params = {};
      if (searchTitle) {
        params["ax"] = searchTitle;
      }
      if (page) {
        params["page"] = page - 1;
      }
      if (pageSize) {
        params["size"] = pageSize;
      }
      if (numEquipe) {
        params["numEquipe"] = numEquipe;
      }
     
      return params;
    };

    //kokoko
    const  retrieveTutorials = () => {
        
        var params = getRequestParams(searchTitle, page, pageSize,num);
        if(succ===true){
          TutorialDataService.findBySlm(params,1)
          .then(response => {
            console.log(response.data);
            const { cc, totalPages } = response.data;
            setCc(cc);
            setCount(totalPages);
          })
          .catch(e => {
            console.log(e);
          })

        }
        else if (named===true){
          
          TutorialDataService.findBySlm(params,2)
          .then(response => {
            const { cc, totalPages } = response.data;
            setCc(cc);
            setCount(totalPages);
          })
          .catch(e => {
            console.log(e);
          })

        }
        else if(both === true){
          
          TutorialDataService.findBySlm(params,3)
          .then(response => {
            const { cc, totalPages } = response.data;
            setCc(cc);
            setCount(totalPages);
          })
          .catch(e => {
            console.log(e);
          })

        }
        else{
        TutorialDataService.findBySlm(params,5)
        .then((response) => {
          const { cc, totalPages } = response.data;
          setCc(cc);
          setCount(totalPages);   
          console.log(params)
        })
        .catch((e) => {
          console.log(e);
        })
      }
    };
      useEffect(retrieveTutorials, [page, pageSize,searchTitle,succ,named,both,num]);
      const checkboxHandler = (e) => {
        setNamed(!named)
      }
      const checkboxHandler1 = (e) => {
        setSucc(!succ);
      }
      
      const checkboxHandler2 = (e) => {
        setBoth(!both);
      }

      // regler
 /*     const findByTitle = () => { 
        setPage(1);  
        TutorialDataService.findByTitle(searchTitle)
          .then(response => {
            const { cc, totalPages } = response.data;
            setCc(cc);
            setCount(totalPages);
          })
          .catch(e => {
            console.log(e);
          });
          retrieveTutorials();
      };
*/    
      const openTutorial = (rowIndex) => {
        const id = tutorialsRef.current[rowIndex].id;
      TutorialDataService.getC(id) 
      .then(response=>{
        history.push("/cc/" + id);
      })
      .catch(e=> {
        console.log(e);
      });
      };
    
      const handlePageChange = (event, value) => {
        setPage(value);
        console.log('hna tbdlt la page')
        console.log(cc)
      };
    
      const handlePageSizeChange = (event) => {
        setPageSize(event.target.value);
        //setPage(1);
      };

const columns = useMemo(
    () => [
      {
        Header: "Nom",
        accessor: "nom",
      },
      {
        Header: "Ax",
        accessor: "ax",
      },
      {
        Header: "succursale",
        accessor: "succ",
      },
      {
        Header: "Numero d'equipe",
        accessor: "numEquipe",
      },
      {
        Header: "More",
        accessor: "actions",
        Cell: (props) => {
          const rowIdx = props.row.id;
          return (
            <div>
              <Button  style={{backgroundColor: '#494949'}}
              onClick={() => openTutorial(rowIdx)}/>
              details
            </div>
          );
        },
      },
    ],
    []
  );
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data: cc,
  });
  
return(
    <div className="list row">
 
    <div className='col-md-3'>
       <label>succursale non renseigne:
          <input type="checkbox" name="nom null" value={succ } 
          defaultChecked={succ} onChange={(e)=>checkboxHandler1(e)} />
        </label>
    </div>
    <div className='col-md-3'>
        <label>nom non renseigne:
        <input type="checkbox" name="nom null" value={named } 
        defaultChecked={named} onChange={(e)=>checkboxHandler(e)} />
        </label> 
    </div>
    <div className='col-md-3'>
        <label>succursale et nom non renseigne:
        <input type="checkbox" value={both }
        defaultChecked={both} onChange={(e)=>checkboxHandler2(e)} />
        </label>
    </div>
  
          <label>Numero d'equipe</label>
            <select as="select" /*value={num}*/  onChange={(e)=>{setNum(e.target.value);console.log(e.target.value);console.log(num) } } >
                        <option value='0' >Select</option>
                        <option value='1'>Equipe 1</option>
                        <option value='Agent' disabled>....</option>
          </select>
      
    
    <div className="col-md-8">
    <div className="input-group mb-3">
        <input
        type="text"
        className="form-control"
        placeholder="Search by Ax"
        value={searchTitle}
        onChange={onChangeSearchTitle}
        />
        <div className="input-group-append">
        <button
            className="btn btn-outline-secondary"
            type="button"
            onClick={findByAx}
        >
            Search
        </button>
        </div>
    </div>
    </div>

    <div className="col-md-12 list">
    <div className="mt-3">
        {"Items per Page: "}
        <select onChange={handlePageSizeChange} value={pageSize}>
        {pageSizes.map((size) => (
            <option key={size} value={size}>
            {size}
            </option>
        ))}
        </select>

        <Pagination
        className="my-3"
        count={count}
        page={page}
        siblingCount={1}
        boundaryCount={1}
        variant="outlined"
        shape="rounded"
        onChange={handlePageChange}
        />
    </div>

    <table
        className="table table-striped table-bordered"
        {...getTableProps()}
    >
        <thead>
        {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
            {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>
                {column.render("Header")}
                </th>
            ))}
            </tr>
        ))}
        </thead>
        <tbody {...getTableBodyProps()}>
        {rows.map((row, i) => {
            prepareRow(row);
            return (
            <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                );
                })}
            </tr>
            );
        })}
        </tbody>
    </table>
    </div>
        
    </div>
)
}
export default CCList ;
