import React, { Component } from "react";
import UserService from "../services/user.service";
import CCList from "./Layout/CCList";
export default class BoardAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: "",
        clients: [],
        currentClt: null,
        currentIndex: -1,
        searchName: "",
    };

  }

  componentDidMount() {
    UserService.getAdminBoard().then(
      response => {
        this.setState({
          content: response.data,
        });
        console.log("Admin => Marketing")
      },
      error => {
        this.setState({
          content:
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString()
        });
      }
    );
  }

  render() {
    
    return (
      <div className="container">
          <header className="jumbotron">
             <h3>Voici la liste des CCs renault :</h3>           
          </header>        
          <CCList/>
      </div>
    );
  }
}
