import React from 'react';
import MyIframe from './myIframe';
import uploadFilesService from '../../services/upload-files.service';
//import Button from 'react-bootstrap/Button'
import { Button } from 'primereact/button';
export default class UploadFiles extends React.Component {
    constructor(props) {
      super(props);
  
      this.state = {
        selectedFiles: undefined,
        currentFile: undefined,
        progress: 0,
        message: "",
  
        fileInfos: [],
      };
      this.selectFile=this.selectFile.bind(this);
      this.upload = this.upload.bind(this);
      
    }
    selectFile(event) {
        this.setState({
          selectedFiles: event.target.files,
        });
      }
 
      upload() {
        let currentFile = this.state.selectedFiles[0];
    
        this.setState({
          progress: 0,
          currentFile: currentFile,
        });
        //console.log(currentFile)
        uploadFilesService.uploadK(currentFile, (event) => {
          this.setState({
            progress: Math.round((100 * event.loaded) / event.total),
          });
        })
          .then((response) => {
            this.setState({
              message: response.data.message,
            });
            return uploadFilesService.getFiles();
          })
          .then((files) => {
            this.setState({
              fileInfos: files.data,
            });
          })
          .catch(() => {
            this.setState({
              progress: 0,
              message: "Could not upload the file!",
              currentFile: undefined,
            });
          });
    
        this.setState({
          selectedFiles: undefined,
        });
      }

      upload1() {
        let currentFile = this.state.selectedFiles[0];
    
        this.setState({
          progress: 0,
          currentFile: currentFile,
        });
        //console.log(currentFile)
        uploadFilesService.upload1(currentFile, (event) => {
          this.setState({
            progress: Math.round((100 * event.loaded) / event.total),
          });
        })
          .then((response) => {
            this.setState({
              message: response.data.message,
            });
            return uploadFilesService.getFiles();
          })
          .then((files) => {
            this.setState({
              fileInfos: files.data,
            });
          })
          .catch(() => {
            this.setState({
              progress: 0,
              message: "Could not upload the file!",
              currentFile: undefined,
            });
          });
    
        this.setState({
          selectedFiles: undefined,
        });
      }
    componentDidMount() {
        console.log("upload a file :")
    }
    render() {
        const {
          selectedFiles,
          currentFile,
          progress,
          message,
          fileInfos,
        } = this.state;
    
        return (
          <div>
          <div>
            {currentFile && (
              <div className="progress">
                <div
                  className="progress-bar progress-bar-info progress-bar-striped"
                  role="progressbar"
                  aria-valuenow={progress}
                  aria-valuemin="0"
                  aria-valuemax="100"
                  style={{ width: progress + "%" }}
                >
                  {progress}%
                </div>
              </div>
            )}
    
            <label className="btn btn-default">
              <input type="file" onChange={this.selectFile} />
            </label>
    
            <button className="btn btn-success"
              disabled={!selectedFiles}
              onClick={this.upload}
            >
              Upload Excel file to bdd
            </button>
    
            <div className="alert alert-light" role="alert">
              {message}
            </div>
    
            <div className="card">
              <div className="card-header">ajouter  la base de donnes</div>
              <ul className="list-group list-group-flush">
                {fileInfos &&
                  fileInfos.map((file, index) => (
                    <li className="list-group-item" key={index}>
                      <a href={file.url}>{file.name}</a>
                    </li>
                  ))}
              </ul>
            </div>
          </div>
          {console.log("file")}
          <div>
            {currentFile && (
              <div className="progress">
                <div
                  className="progress-bar progress-bar-info progress-bar-striped"
                  role="progressbar"
                  aria-valuenow={progress}
                  aria-valuemin="0"
                  aria-valuemax="100"
                  style={{ width: progress + "%" }}
                >
                  {progress}%
                </div>
              </div>
            )}
    
            <label className="btn btn-default">
              <input type="file" onChange={this.selectFile} />
            </label>
    
            <button className="btn btn-success"
              disabled={!selectedFiles}
              onClick={this.upload1}
            >
              Upload Kilometrage file to bdd
            </button>
    
            <div className="alert alert-light" role="alert">
              {message}
            </div>
    
            <div className="card">
              <div className="card-header">Ajouter  la base de donnes kilometrage</div>
              <ul className="list-group list-group-flush">
                {fileInfos &&
                  fileInfos.map((file, index) => (
                    <li className="list-group-item" key={index}>
                      <a href={file.url}>{file.name}</a>
                    </li>
                  ))}
              </ul>
            </div>
          </div>

          </div>
        );
      }
  
    }