import React, { Component } from "react";
import UserService from "../services/user.service";
import Form from "react-bootstrap/Form";
export default class Client extends Component {
  constructor(props) {
    super(props);
    this.onChangeName = this.onChangeName.bind(this);
    this.onChangeCode = this.onChangeCode.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangeAdresse = this.onChangeAdresse.bind(this);
    this.onChangeCodePostal = this.onChangeCodePostal.bind(this);
    this.onChangeVille = this.onChangeVille.bind(this);
    this.onChangeNumTel1 = this.onChangeNumTel1.bind(this);
    this.onChangeNumTel2 = this.onChangeNumTel2.bind(this);
    this.onChangeCommentaire = this.onChangeCommentaire.bind(this);
    this.onChangeSexe = this.onChangeSexe.bind(this);

    this.getClient = this.getClient.bind(this);
    this.updatePublished = this.updatePublished.bind(this);
    this.updateClient = this.updateClient.bind(this);
    this.deleteClient = this.deleteClient.bind(this);
    this.onChangeTypo = this.onChangeTypo.bind(this);

    this.state = {
      currentClient: {
        id: null,
        name: "",
        typologie: "",
        email :"",
        adresse:"",
        sexe : null ,
        ville:"",
        codePostal:null,
        num_tel1:null,
        num_tel2:null,
        commentaire:"",
        published: false
      },
      message: ""
    };
  }

  componentDidMount() {
    this.getClient(this.props.match.params.id);
  }

  onChangeName(e) {
    const name = e.target.value;

    this.setState(function(prevState) {
      return {
        currentClient: {
          ...prevState.currentClient,
          name: name
        }
      };
    });
  }
  
  onChangeSexe(event){
    (event.target.value==="Femme") ? 
    (this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        sexe : true
      }
    })) ) 
    :  
    (this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        sexe : false
      }
    })) );
  }

  onChangeCode(e) {
    const code = e.target.value;
    
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        code: code
      }
    }));
  }

  onChangeEmail(e) {
    const email = e.target.value;
    
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        email: email
      }
    }));
  }
  onChangeAdresse(e) {
    const adresse = e.target.value;
    
    this.setState(prevState => ({
      currentClient: {
        ...prevState.currentClient,
        adresse: adresse
      }
    }));
  }
  onChangeVille(e){
    const ville = e.target.value;
    this.setState(prevState=>({
      currentClient:{
        ...prevState.currentClient,
        ville:ville
      }
    }));
  }
  onChangeCodePostal(e){
    const codePostal = e.target.value;
    this.setState(prevState=>({
      currentClient:{
        ...prevState.currentClient,
        codePostal:codePostal
      }
    }));
  }
  onChangeNumTel1(e) {
    const num_tel1 = e.target.value;
    this.setState(prevState => ({
      currentClient:{
        ...prevState.currentClient,
        num_tel1 : num_tel1
      }
    }));
  }
  onChangeNumTel2(e) {
    const num_tel2 = e.target.value;
    this.setState(prevState => ({
      currentClient:{
        ...prevState.currentClient,
        num_tel2 : num_tel2
      }
    }));
  }
  onChangeCommentaire(e){
    const commentaire = e.target.value;
    this.setState(prevState=>({
      currentClient:{
        ...prevState.currentClient,
        commentaire:commentaire
      }
    }));
  }
  onChangeTypo(e){
    const type = e.target.value;
    this.setState(prevState =>({
      currentClient:{
        ...prevState.currentClient,
        typologie : type 
      }
    }));
  }
  getClient(id) {
    UserService.get(id)
      .then(response => {
        this.setState({
          currentClient: response.data
        });
      })
      .catch(e => {
        console.log(e);
      });
  }

  updatePublished(status) {
    var data = {
      id: this.state.currentClient.id,
      name: this.state.currentClient.name,
      code: this.state.currentClient.code,
      published: status
    };

    UserService.update(this.state.currentClient.id, data)
      .then(response => {
        this.setState(prevState => ({
          currentClient: {
            ...prevState.currentClient,
            published: status
          }
        }));
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }



  updateClient() {
    UserService.update(
      this.state.currentClient.id,
      this.state.currentClient
    )
      .then(response => {
        this.setState({
          message: "The client was updated successfully!"
        });
        this.props.history.push('/mod')
      })
      .catch(e => {
        console.log(e);
      });
  }
//---- retour en arrire
  deleteClient() {    
        this.props.history.push('/mod');
  }

  render() {
    const { currentClient } = this.state;
    //const sexe ='' ;
    //currentClient.sexe ==true ? sexe = 'Femme' : sexe = 'Homme';
    
    return (
      <div>
        {currentClient ? (
          <div className="edit-form">
            <h4>Client</h4>
              <Form>
                  <Form.Group controlId="formBasicName">
                    <Form.Label>Nom</Form.Label>
                    <Form.Control type="text" value={currentClient.name} onChange={this.onChangeName} placeholder="Enter Le nom du client" />
                  </Form.Group>
                  <Form.Group  controlId="exampleForm.Select2">
                    <Form.Label>Typologie</Form.Label>
                    <Form.Control as="select" value={currentClient.typologie}  onChange={this.onChangeTypo} size="sm" custom>
                      <option value='Taxi'>Taxi</option>
                      <option value='Societe'>Societe</option>
                      <option value='Agent'>Agent</option>
                      <option value='Particulier'>Particulier</option>
                      <option value='Loueur'>Loueur</option>
                    </Form.Control>
                  </Form.Group>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" value={currentClient.email} onChange={this.onChangeEmail} placeholder="Enter email" />
                  </Form.Group>
               
                  <Form.Group controlId="formBasicAdresse">
                    <Form.Label>Adresse</Form.Label>
                    <Form.Control type="text" value={currentClient.adresse} onChange={this.onChangeAdresse} placeholder="Saisissez l'adresse" />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.Select5">
                    <Form.Label>Sexe</Form.Label>
                    <Form.Control  as="select" custom value={currentClient.sexe} onChange={this.onChangeSexe} size="sm" placeholder="Sexe">
                      <option value="Homme">Homme</option>
                      <option value="Femme">Femme</option>
                    </Form.Control>
                  </Form.Group>
                  <Form.Group  controlId="formBasicVille">
                    <Form.Label>Ville</Form.Label>
                    <Form.Control type="text" value={currentClient.ville} onChange={this.onChangeVille} placeholder="Saisissez la ville" />
                  </Form.Group>
                  <Form.Group controlId="formBasicCodePostal">
                    <Form.Label>Code Postal</Form.Label>
                    <Form.Control type="tel"  value={currentClient.codePostal} onChange={this.onChangeCodePostal} placeholder="Saisissez le Code Postal" />
                  </Form.Group>
                  <Form.Group controlId="formBasicNumTel1">
                    <Form.Label>Numro de tlephone principal</Form.Label>
                    <Form.Control type="tel"  value={currentClient.num_tel1} onChange={this.onChangeNumTel1} placeholder="Saisissez le Numro telephone principale du client" />
                  </Form.Group>
                  <Form.Group controlId="formBasicNumTel2">
                    <Form.Label>Numro de tlephone secondaire</Form.Label>
                    <Form.Control type="tel"  value={currentClient.num_tel2} onChange={this.onChangeNumTel2} placeholder="Saisissez le Numro de telephone secondaire du client" />
                  </Form.Group>
              </Form> 
               
              <div className="form-group">
                <label>
                  <strong>Status:</strong>
                </label>
                {currentClient.published ? "client non_verifi" : "client verifi"}
              </div>
              <Form>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                    <Form.Label>Commentaire</Form.Label>
                    <Form.Control as="textarea" value={currentClient.commentaire} onChange={this.onChangeCommentaire} placeholder="Laisser un Commentaire" />
                </Form.Group>
            </Form>
            
            <br/>
            {currentClient.published ? (
              <button
                className="badge badge-primary mr-2"
                onClick={() => this.updatePublished(false)}
              >
                client non_verifi
              </button>
            ) : (
              <button
                className="badge badge-primary mr-2"
                onClick={() => this.updatePublished(true)}
              >
                Client Verifi
              </button>
            )}          
           
            <button
              type="submit"
              className="badge badge-success"
              onClick={this.updateClient}
              
            >
              Mettre  jour
            </button>
            <button
              className="badge badge-danger mr-2"
              onClick={this.deleteClient}
              
            >
              retour
            </button>
            
            <p>{this.state.message}</p>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Client...</p>
          </div>
        )}
      </div>
    );
  }
}


