import React, { Component } from "react";
//import UserService from "../services/user.service";
import ClientList from "./Layout/ClientList";
import Button from 'react-bootstrap/Button';
//import {SliderDemo} from "./Layout/SliderDemo";
import AuthService from '../services/auth.service';
import { Col } from "react-bootstrap";
import Form from 'react-bootstrap/Form';
import { DatePicker } from 'rsuite';
import 'rsuite/dist/styles/rsuite-default.css';
export default class BoardCC extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: "",
      clients: [],
      searchTitle:"",
      sexe : "" ,
      typologie:"",
      marque : "",
      modele : "",
      ax:"",
      pt:"",
      modalite:"",
      commerciale:"",
      slm:1,
      endDate:"",
      startDate:"",
      rangemin:0,
      rangemax:1000,
      range:[1000,20000],
    };
    this.onChangeSexe= this.onChangeSexe.bind(this);
    this.onChangeTypo= this.onChangeTypo.bind(this);
    this.onChangeMarque = this.onChangeMarque.bind(this);
    this.onChangeModele = this.onChangeModele.bind(this);
    this.onChangeMod= this.onChangeMod.bind(this);
    this.onChangeSearchTitle = this.onChangeSearchTitle.bind(this);
    this.onChangeStartDate = this.onChangeStartDate.bind(this);
    this.onChangeEndDate = this.onChangeEndDate.bind(this);
    this.onChangeMin=this.onChangeMin.bind(this);
    this.onChangeMax=this.onChangeMax.bind(this);
  }
  onChangeSearchTitle(e){
    this.setState({searchTitle:e.target.value});
  }
  componentDidMount() {
    const currentUser = AuthService.getCurrentUser();
    this.setState({ax:currentUser.username})
    console.log('cc => 1');
    
  }

  onChangeMin(event){
    this.setState({rangemin:event.target.value})
  }
  onChangeMax(event){
    this.setState({rangemax:event.target.value})
  }
  componentWillUnmount(){
    this.setState({
      content: "",
        clients: [],
        sexe : null ,
        typologie:""
    });
  }

  onChangeSexe(event){
    this.setState({sexe:event.target.value});
  }
  
  onChangeTypo(event){
    this.setState({ typologie : event.target.value});
  }
  onChangeMod(event){
    this.setState({ modalite : event.target.value});
  }

  
  onChangeMarque(e){
    this.setState({ marque : e.target.value});
  }
  
  onChangeModele(e){
  this.setState({modele:e.target.value})
  }
  onChangeStartDate(value) {
    var today = value;
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    today = yyyy + '-' + mm + '-' + dd;
    console.log(today);
    this.setState({
      startDate:today,
    });
  }
  onChangeEndDate(value) {
    var today = value;
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    today = yyyy + '-' + mm + '-' + dd;
    console.log(today);
    this.setState({
      endDate:today,
    });
  }
  render() {
    
    return (
      <div className="container">
        <header className="jumbotron">
        <Form onSubmit={this.onFormSubmit} role="form">
          <Form.Row>
          <Form.Group as={Col} controlId="exampleForm.Select8" >
                  <Form.Label>Select Start Date :</Form.Label>
                 {/*<DateRangePicker />*/}
                 <DatePicker dateFromat='YYYY-MM-dd' style={{ width: 280 }} value={this.state.startDate} onChange={this.onChangeStartDate}/>
            </Form.Group>
            <Form.Group as={Col} controlId="exampleForm.Select8" >
                  <Form.Label>Select End Date :</Form.Label>
                 {/*<DateRangePicker />*/}
                 <DatePicker style={{ width: 280 }} dateFromat='YYYY-MM-dd' value={this.state.endDate} onChange={this.onChangeEndDate}/>
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.Select">
                <Form.Label>Marque</Form.Label>
                <Form.Control as="select" custom onChange={this.onChangeMarque} size="sm" >
                <option value="" disabled selected>Select your option</option>
                  <option value="Renault">Renault</option>
                  <option value="Dacia">Dacia</option>
                </Form.Control>
            </Form.Group>
           {/* remplir dynamiquement depuis la bdd (if possible)*/} 
           <Form.Group as={Col} controlId="exampleForm.Select1">
              <Form.Label>Modle</Form.Label>
              <Form.Control as="select" onChange={this.onChangeModele} size="sm" custom>
                <option value="" disabled selected>Select your option</option>
                <option value="52B">52B</option>
                <option value="52L">52L</option>
                <option value="67F">67F</option>
                <option value="67P">67P</option>
                <option value="79H">79H</option>
                <option value="87J">87J</option>
                <option value="90B">90B</option>
                <option value="90L">90L</option>
                <option value="92J">92J</option>
                <option value="CL2">CL2</option>
                <option value="CL4">CL4</option>
                <option value="CT5">CT5</option>
                <option value="FLU">FLU</option>
                <option value="KAN">KAN</option>
                <option value="KEX">KEX</option>
                <option value="KOL">KOL</option>
                <option value="KP2">KP2</option>
                <option value="KU2">KU2</option>
                <option value="LC3">LC3</option>
                <option value="M32">M32</option>
                <option value="M3B">M3B</option>
                <option value="M3D">M3D</option>
                <option value="MAU">MAU</option>
                <option value="S3C">S3C</option>
                <option value="S3L">S3L</option>
                <option value="SFN">SFN</option>
                <option value="TFP">TFP</option>
                <option value="TFU">TFU</option>
                <option value="TW2">TW2</option>
              </Form.Control>
            </Form.Group>
           {/* kilometrage a ajouter  */}
          </Form.Row>
          
          <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.Select5">
              <Form.Label>Sexe</Form.Label>
              <Form.Control  as="select" custom onChange={this.onChangeSexe} size="sm" >
                <option value="" disabled selected>Select your option</option>
                <option value="Homme">Homme</option>
                <option value="Femme">Femme</option>
                <option value="Other">Other</option>
              </Form.Control>
            </Form.Group>
            <Form.Group as={Col} controlId="exampleForm.Select2">
              <Form.Label>Typologie</Form.Label>
              <Form.Control as="select" onChange={this.onChangeTypo} size="sm" custom>
                <option value="" disabled selected>Select your option</option>
                <option value='Taxi'>Taxi</option>
                <option value='Societe'>Societe</option>
                <option value='Agent'>Agent</option>
                <option value='PARTICULIE'>Particulier</option>
                <option value='REVENDEUR'>REVENDEUR</option>
                <option value='PGR FAMILL'>PGR FAMILL</option>
                <option value='PGR'>PGR</option>
                <option value='PERSONNEL'>PERSONNEL</option>
                <option value='PART-SUCC'>PART-SUCC</option>
                <option value='Loueur'>Loueur</option>
                <option value='CONVENTION'>CONVENTION</option>
                <option value='AGENT S'>AGENT S</option>
                <option value='AGENT'>AGENT</option>
                <option value='GROSSISTE'>GROSSISTE</option>
                <option value='GARAGE'>GARAGE</option>
              </Form.Control>
            </Form.Group>
            <Form.Group as={Col} controlId="exampleForm.Select6"  onChange={this.onChangeMod}>
              <Form.Label>Modalit de paiement</Form.Label>
              <Form.Control as="select"  size="sm" custom>
                <option value="" disabled selected>Select your option</option>
                <option value='Comptant'>Comptant</option>
                <option value='Credit'>Credit</option>
              </Form.Control>
            </Form.Group>
          </Form.Row>  

          <Form.Row>
                <Form.Group as={Col} controlId="formBasicRange">
                  <Form.Label>Kilometrage Minimum: {this.state.rangemin}</Form.Label>
                  <Form.Control type="range" min="0" max="20000" onChange={this.onChangeMin}/>
                </Form.Group>
                <Form.Group as={Col} controlId="formBasicRange1">
                  <Form.Label>Kilometrage Maximum: {this.state.rangemax}</Form.Label>
                  <Form.Control style={{"direction":"rtl"}} min="1000" max="50000" type="range" onChange={this.onChangeMax}/>
                </Form.Group>
              </Form.Row>  
            <Form.Group  controlId="exampleForm.Submit">
              <Form.Label>Refresh filter :</Form.Label>
              <Button type="submit" >Refresh</Button>      
            </Form.Group>
        </Form>
        <div className="col-md-8">

        <div className="input-group mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="Search by title"
            value={this.state.searchTitle}
            onChange={this.onChangeSearchTitle}
          />
        </div>
        
        </div>
        </header> 
        <ClientList sexe={this.state.sexe} user={this.state.ax} typologie={this.state.typologie} marque = {this.state.marque} modele ={this.state.modele} pt={this.state.pt} modalite={this.state.modalite} commerciale = {this.state.commerciale} searchTitle={this.state.searchTitle} slm={this.state.slm} startDate={this.state.startDate} endDate={this.state.endDate} />
      </div>
    );
  }
}
