import React,{useState,useEffect} from 'react';
import { Pie } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import outLabels from 'chartjs-plugin-piechart-outlabels';
const PieChart = (props) => {
const [value, setValue] = useState(props.clients);

useEffect(() => { setValue(props.temoin) }, [props.temoin]);
const options = {
  legend:{display:true,
    //fullwidth:false,'chartArea'
    
    labels:{
      boxWidth:5,
      fontSize:7,
      usePointStyle:true,
      
    }},
  responsive: true,
  maintainAspectRatio: true,
  plugins: {
    datalabels: {
      formatter: (value, ctx) => {
        let sum = 0;
        let dataArr = ctx.chart.data.datasets[0].data;
        dataArr.map(data => {
            sum += data;
        });
        let percentage = (value*100 / sum).toFixed(2)+"%";
        return percentage;
      },
      align: 'end',
      display:'auto',
      //anchor: 'end',
      color: 'black',
      font: {
        size: 10,
      },
      offset: 10,
      padding: 0,
      
    },
    outlabels: {
      text: '%l ',
      stretch:20,
      color: 'black',
    }
    
  }
  
}

    return(
      <> 
        <Pie   data={props.data} redraw options= {options }  plugins={[ChartDataLabels,outLabels] } />
      </>
    );
};

export default PieChart;

