import React, { useState, useEffect, useMemo, useRef } from "react";
import Pagination from "@material-ui/lab/Pagination";
import TutorialDataService from "../services/TutorialService";
import { useTable } from "react-table";
import { useHistory } from "react-router-dom";
import { Button } from 'primereact/button';
import userService from '../services/user.service'
const Predict1 = (props) => {
  const [clients, setclients] = useState([]);
  //const [searchTitle, setSearchTitle] = useState("");  
  const tutorialsRef = useRef();
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);
  const [pageSize, setPageSize] = useState(6);
  const history = useHistory()
  const pageSizes = [3,6,9];
  tutorialsRef.current = clients;
  const getRequestParams = (page, pageSize) => {
    let params = {};
    if (page) {
      params["page"] = page - 1;
    }
    if (pageSize) {
      params["size"] = pageSize;
    }
    return params;
  };
  const retrieveTutorials = () => {
    const params = getRequestParams(page, pageSize);
    userService.predict(params)
        .then((response)=>{
          const { clients, totalPages } = response.data;
          setclients(clients);
          console.log("total pages :"+totalPages);
          setCount(totalPages);
          console.log("count :"+count);

        })
        .catch((e) => {
          console.log(e);
        })
  }
  
  
  
  useEffect(retrieveTutorials, [page, pageSize]);

  const openTutorial = (rowIndex) => {
    console.log("id :"+tutorialsRef.current[rowIndex].id)
    const id = tutorialsRef.current[rowIndex].id;
      TutorialDataService.get(id) 
      .then(response=>{
        history.push("/api/clients/" + id);
      })
      .catch(e=> {
        console.log(e);
      });
  };

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  const handlePageSizeChange = (event) => {
    setPageSize(event.target.value);
    setPage(1);
  };
const columns = useMemo(
    () => [
      {
        Header: "Name",
        accessor: "nom",
      },
      {
        Header: "Typologie",
        accessor: "typologie",
      },
      {
        Header: "Marque",
        accessor: "marque",
      },
      {
        Header: "Modele",
        accessor: "modele",
      },
      {
        Header: "succursale",
        accessor: "succersale",
        
      },
      {
        Header: "Code Vendeur",
        accessor: "vendeur",
        
      },
      {
        Header: "Date Comptabilisation",
        accessor: "dateComptabilisation",
        
      },
      {
        Header: "Actions",
        accessor: "actions",
        Cell: (props) => {
          const rowIdx = props.row.id;
          return (
            <div>
              <Button   style={{backgroundColor: '#494949'}}
              onClick={() => openTutorial(rowIdx)}/>
              details
            </div>
          );
        },
      },
    ],
    []
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data: clients,
  });

  return (
    <div className="list row">

      <div className="col-md-12 list">
        <div className="mt-3">
          {"Items per Page: "}
          <select onChange={handlePageSizeChange} value={pageSize}>
            {pageSizes.map((size) => (
              <option key={size} value={size}>
                {size}
              </option>
            ))}
          </select>

          <Pagination
            className="my-3"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
          />
        </div>

        <table
          className="table table-striped table-bordered"
          {...getTableProps()}
        >
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row, i) => {
              prepareRow(row);
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map((cell) => {
                    return (
                      <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                    );
                  })}
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
      
    </div>
  );
};

export default Predict1;

