import React, { Component } from "react";
import "./index.css"
export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: ""
    };
  }
  render() {
    return (   
              <div className="container"  >                  
                <section className='landing'>
                      <div className='dark-overlay'>
                        <div className='landing-inner'>
                          <h1 className='x-large'>Manage customers</h1>
                          <p className='lead'>
                            Login to your account,<br/>
                            Get acces to your own privileges: <br/>Filter,visualise and predict.
                          </p>                  
                        </div>
                      </div>
                </section>  
              </div>
    );
  }
}
