  getFilters(sexe, marque, modele,typologie,pt,modalite,commerciale,startDate,endDate){
    let params = {};
    if (sexe) {
      params["sexe"] = sexe;
    }
    if (marque) {
      params["marque"] = marque ;
    }
    if (modele) {
      params["modele"] = modele;
    }
    if (typologie) {
      params["typologie"] = typologie;
    }
    if (pt) {
      params["pointVente"] = pt;
    }
    if (modalite) {
      params["modalitePaiement"] = modalite;
    }
    if (commerciale) {
      params["nomVendeur"] = commerciale;
    }
    if (startDate) {
      params["startDate"] = startDate;
    }
    if (endDate) {
      params["endDate"] = endDate;
    }
    return params;
  };
