import React, { useState, useEffect, useMemo, useRef } from "react";
import Pagination from "@material-ui/lab/Pagination";
import TutorialDataService from "../../services/TutorialService";
import { useTable } from "react-table";
import { useHistory } from "react-router-dom";
import { Button } from 'primereact/button';
//import AuthService from '../../services/auth.service';
import userService from '../../services/user.service'
const TutorialsList = (props) => {
  const [clients, setclients] = useState([]);
  //const [searchTitle, setSearchTitle] = useState("");  
  const tutorialsRef = useRef();
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);
  const [pageSize, setPageSize] = useState(6);
  const history = useHistory()
  const pageSizes = [3,6,9];
  tutorialsRef.current = clients;

  const getRequestParams = (searchTitle, page, pageSize) => {
    let params = {};
    if (searchTitle) {
      params["name"] = searchTitle;
    }
    if (page) {
      params["page"] = page - 1;
    }
    if (pageSize) {
      params["size"] = pageSize;
    }
    return params;
  };
  const getFilters = (sexe, marque, modele,typologie,pt,modalite,commerciale,slm,startDate,endDate,min,max) => {
    let params = {};
    //console.log(slm)
    if (sexe) {
      params["sexe"] = sexe;
    }
    if (marque) {
      params["marque"] = marque ;
    }
    if (modele) {
      params["modele"] = modele;
    }
    if (typologie) {
      params["typologie"] = typologie;
    }
    if (pt) {
      params["pointVente"] = pt;
    }
    if (modalite) {
      params["modalitePaiement"] = modalite;
    }
    if (commerciale) {
      params["nomVendeur"] = commerciale;
    }
    if (startDate) {
      params["startDate"] = startDate;
    }
    if (endDate) {
      params["endDate"] = endDate;
    }
    if(min){
      params["kilometrage"] = min;
    }
    if(max){
      params["kilometrage2"] = max;
    }
    if(slm==1){
      //console.log("skhol")
      params["slm"]=slm;
    }  
    /*const currentUser = AuthService.getCurrentUser();
    params["user"]=currentUser.username;*/
    return params;
  };

  const retrieveTutorials = () => {
    const params = getRequestParams(props.searchTitle, page, pageSize);
    const params1 = getFilters(props.sexe, props.marque, props.modele,props.typologie,props.pt,props.modalite,props.commerciale,props.slm,props.startDate,props.endDate,props.min,props.max)
    userService.retrievetest(params,params1)
        .then((response)=>{
          const { clients, totalPages } = response.data;
          const keys = ["id","nom","typologie","marque","modele","succersale","vendeur","dateC"];
          var formatted = [],
          data = clients,
          cols = keys,
          l = cols.length;
          for (var i=0; i<data.length; i++) {
                  var d = data[i],
                          o = {};
                  for (var j=0; j<l-1; j++)
                          o[cols[j]] = d[j];
                  //console.log(d[l-1].slice(0,10))
                  
                  o[cols[l-1]] =d[l-1].slice(0,10);       
                  formatted.push(o);
          }
          setclients(formatted);
          //setclients(clients);
          setCount(totalPages);
          console.log(formatted)
          console.log(props.endDate)
        })
        .catch((e) => {
          console.log(e);
        })
  }
  
  
  
  useEffect(retrieveTutorials, [page, pageSize,props.sexe ,props.typologie,props.marque , props.modele,props.commerciale, props.modalite,props.pt,props.searchTitle,props.startDate,props.endDate,props.min,props.max]);

  const openTutorial = (rowIndex) => {
    const id = tutorialsRef.current[rowIndex].id;
      TutorialDataService.get(id) 
      .then(response=>{
        history.push("/api/clients/" + id);
      })
      .catch(e=> {
        console.log(e);
      });
  };

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  const handlePageSizeChange = (event) => {
    setPageSize(event.target.value);
    console.log(pageSize);
    setPage(1);
  };
const columns = useMemo(
    () => [
      {
        Header: "Name",
        accessor: "nom",
      },
      {
        Header: "Typologie",
        accessor: "typologie",
      },
      {
        Header: "Marque",
        accessor: "marque",
      },
      {
        Header: "Modele",
        accessor: "modele",
      },
      {
        Header: "succursale",
        accessor: "succersale",
        
      },
      {
        Header: "Code Vendeur",
        accessor: "vendeur",
        
      },
      {
        Header: "Date Comptabilisation",
        accessor: "dateC",
        
      },
      {
        Header: "Actions",
        accessor: "actions",
        Cell: (props) => {
          const rowIdx = props.row.id;
          return (
            <div>
              <Button   style={{backgroundColor: '#494949'}}
              onClick={() => openTutorial(rowIdx)}/>
              details
            </div>
          );
        },
      },
    ],
    []
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data: clients,
  });

  return (
    <div className="list row">

      <div className="col-md-12 list">
        <div className="mt-3">
          {"Items per Page: "}
          <select onChange={handlePageSizeChange} value={pageSize}>
            {pageSizes.map((size) => (
              <option key={size} value={size}>
                {size}
              </option>
            ))}
          </select>

          <Pagination
            className="my-3"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
          />
        </div>

        <table
          className="table table-striped table-bordered"
          {...getTableProps()}
        >
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row, i) => {
              prepareRow(row);
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map((cell) => {
                    return (
                      <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                    );
                  })}
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
      
    </div>
  );
};

export default TutorialsList;

