import React, { Component } from "react";
import UserService from "../services/user.service";
import { Link } from "react-router-dom";

export default class clientsList extends Component {
  constructor(props) {
    super(props);
    this.onChangeSearchName= this.onChangeSearchName.bind(this);
    this.retrieveClients = this.retrieveClients.bind(this);
    this.refreshList = this.refreshList.bind(this);
    this.setActiveClient = this.setActiveClient.bind(this);
    this.removeAllClients = this.removeAllClients.bind(this);
    this.searchName = this.searchName.bind(this);   
    this.state = {
      clients: [],
      currentClt: null,
      currentIndex: -1,
      searchName: "",
    };     
  }

  componentDidMount() {
    this.retrieveClients();
  }

  onChangeSearchName(e) {
    const searchName = e.target.value;

    this.setState({
      searchName: searchName
    });
  }
  retrieveClients() {

    UserService.getAll()
      .then((response) => {
        
        this.setState({
          clients: response.data,
          
        });
        console.log(this.state.clients);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  refreshList() {
    this.retrieveClients();
    this.setState({
      currentClt: null,
      currentIndex: -1
    });
    console.log()
  }

  setActiveClient(client, index) {
    this.setState({
      currentClt: client,
      currentIndex: index
    });
  }

  removeAllClients() {
    UserService.deleteAll()
      .then(response => {
        console.log(response.data);
        this.refreshList();
      })
      .catch(e => {
        console.log(e);
      });
  }

  searchName() {
    this.setState({
      currentClt: null,
      currentIndex: -1
    });

    UserService.findByName(this.state.searchName)
      .then(response => {
        this.setState({
          clients: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }
//const{count,page,pageSize, }
  render() {
    const { searchName, clients,currentClt, currentIndex, } = this.state;


    return (
//search Bar.      
      <div className="list row">
        <div className="col-md-8">
          <div className="input-group mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Search by Name"
              value={searchName}
              onChange={this.onChangeSearchName}
            />
            <div className="input-group-append">
              <button
                className="btn btn-outline-secondary"
                type="button"
                onClick={this.searchName}
              >
                Search
              </button>
            </div>
          </div>
        </div>
 
        <div className="col-md-6">
          <h4>Client List</h4>
          <ul className="list-group">
            {clients &&
              clients.map((client, index) => (
                <li
                  className={
                    "list-group-item " +
                    (index === currentIndex ? "active" : "")
                  }
                  onClick={() => this.setActiveClient(client, index)}
                  key={index}
                >
                  {client.name}
                </li>
              ))}
          </ul>
          
          <button
            className="m-3 btn btn-sm btn-danger"
            onClick={this.removeAllClients}
          >
            Remove All
          </button>
          
        </div>
        <div className="col-md-6">
          {currentClt ? (
            <div>
              <h4>Client</h4>
              <div>
                <label>
                  <strong>Name:</strong>
                </label>{" "}
                {currentClt.name}
              </div>
              <div>
                <label>
                  <strong>Code Client:</strong>
                </label>{" "}
                {currentClt.code}
              </div>
              <div>
                <label>
                  <strong>Sexe:</strong>
                </label>{" "}
                {currentClt.sexe ? "femme" : "homme"}
              </div>

              <Link 
                to={"/clients/" + currentClt.id}
                className="badge badge-info"
                style={{color: "#000"}}
              >
                More
              </Link>
            </div>
          ) : (
            <div>
              <br />
              <p>Please click on a Client...</p>
            </div>
          )}
        </div>
      </div>
    );
  }
}