import http from "../http-common";

const getC = (id) => {
  return http.get(`/cc/${id}`);
};

const findByLbs =(params) => {
  return http.get('/cc/Succ',params);
}
const findByAx = (ax) => {
  return http.get(`/cc?ax=${ax}`);
};
const findBySlm =(param,count) => {
  return http.get(`/cc/Succ`,{params:{
              count: count ,
              page:param.page,
              size:param.size,
              numEquipe:param.numEquipe,
        }}
        
        );
}
const get = (id) => {
  return http.get(`/clients/${id}`);
};
const TutorialService = {
  getC,
  findByLbs,
  findBySlm,
  findByAx,
  get,
};



export default TutorialService;

